import styled from "styled-components";

const SliderButtonStyled = styled.div`
    background-color: white;
    padding: 2rem;
    border-radius: 50%;
    opacity: 0.4;
    margin: 0 1rem;

    &:hover{
    opacity: 1.0;
    cursor: pointer;
    }
`;

export default SliderButtonStyled;